#btn_login{
    justify-content: center;
}
#login_form_cont{
    position: relative;
    justify-content: center;
    text-align: center;
}
.contanier-loading::after{
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    content: '';
    background-color: rgba(0,0,0, .2);
    border-radius:10px;
    width: 100%;
    height: 100%;
    z-index: 100;
}
.contanier-loading::before{
    display: block;
    position: absolute;
    content: '';
    top:0;
    left:0;
    bottom:0;
    right:0;
    margin:auto;
    width: 80px;
    height: 80px;
    border: 10px solid #003770;
    border-left: 10px solid #e30613;
    border-radius: 50%;
    animation-name: snipper;
    animation-duration: .7s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    z-index:200;
}

@keyframes snipper {
    to{
        transform: rotate(360deg);
    }
}
#login_form_cont p{
    margin: 0;
}

#login_form_cont a{
    text-decoration: none;
    font-size: 13px;
}


